.expenses-table {
  display: inline-block;
  margin: 0 10px;
}

.expenses-table th {
  background-color: white;
  border: 1px solid black;
  border-radius: 6px;
  padding: 15px 20px;
  white-space: pre-wrap;
  width: 110px;
}

.expenses-table td {
  border: 1px solid #212121;
  border-radius: 4px;
  padding: 10px 20px;
  text-align: center;
}

.delete-button {
  background-color: rgb(19, 18, 18);
  border: none;
  border-radius: 6px;
  color: white;
  margin: 10px 45px;
  padding: 8px 12px;
}

.delete-button:hover {
  background-color: black;
  transition: 0.8s;
}

@media only screen and ( max-width : 1190px ) {

  .expenses-table {
    display: inline-block;
    white-space: pre-wrap;
  }

  .expenses-table th {
    background-color: white;
    border: 1px solid black;
    border-radius: 6px;
    padding: 15px 5px;
  }

  .expenses-table td {
    border: 1px solid #212121;
    border-radius: 4px;
    text-align: center;
  }

  .delete-button {
    background-color: rgb(19, 18, 18);
    border: none;
    border-radius: 6px;
    color: white;
    margin: 10px 45px;
    padding: 8px 12px;
  }

  .delete-button:hover {
    background-color: black;
    transition: 0.8s;
  }
}
